.records-list {
    height: calc(100% - 79px);
    overflow-y: scroll;
    margin-top: 28px;
}

.loading-title {
    font-size: 30pt;
    font-family: Roboto;
    color: #41B883;
}

.speaker-page {
    &__loading-title {
        margin-top: 50%;
    }
}
