import Vue from "vue"
import Router from 'vue-router'
import Microphone from '@/views/Microphone'
import RecordsList from '@/views/RecordsList'
import Speaker from '@/views/Speaker'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			component: Microphone
		},
		{
			path: '/list',
			component: RecordsList
 		},
 		{
 			path: '/speaker',
 			component: Speaker
 		}
	]
})